import {useRef} from 'react'
import SymbolInfo from "./SymbolInfo";
import styled from "styled-components";
import SymbolPanel from "./SymbolPanel";
import ReactTooltip from 'react-tooltip'
import './symbolArea.less'
import { inject, observer } from "mobx-react";
import Tooltip from "../../../../components/Tooltip/Tooltip";

function SymbolArea({trading,lang}){
  const toolTipRef = useRef()
  const calculatePosition = (position,currentEvent,currentTarget,refNode,place,desiredPlace,effect,offset) => {
      const rect = currentTarget.getBoundingClientRect();
      const top = rect.top + rect.height;
    return {top:top ,left : rect.left}
  }
  return (
    <div className='sa-main'>
      <SymbolInfo  lang={lang} trading={trading} toolTipRef={toolTipRef}/>
      <Tooltip ref={toolTipRef} id='symbol-panel'width={352} place='bottom' overridePosition={calculatePosition}
         resizeHide={false} eventOff={null} scrollHide={false} className='s-react-tooltip' type="info" globalEventOff='click' backgroundColor='#203B60' clickable >
        <SymbolPanel lang={lang} pool={trading.pool} symbols={trading.symbolInfos}/>
      </Tooltip>
    </div>
  )
}
// export default SymbolArea;
export default inject('trading')(observer(SymbolArea))