import styled from 'styled-components'
import Tab from '../../../components/Tab/Tab'
import Position from './Position/Position'
import History from './History/History'
import Icon from '../../../components/Icon/Icon'

const Wrapper = styled.div`
  padding : 24px 24px 0 32px;
  position : relative;
  .slide-icon {
    display : none;
  }

  @media only screen and (max-width : 760px) {
    padding : 24px 24px 0 24px;
    .slide-icon {
      position: absolute;
      top: 0;
      display: flex;
      align-items: center;
      background: #0A172B;
      height: 100%;
    }
    .slide-icon.right {
      right: 0px;
    }

    .slide-icon.left {
      left: 0px;
    }
  }
`
export default function MyInfo({lang}){
 
  const tabSet = {
    [lang['positions']] : {content : <Position lang={lang}/>},
    [lang['history']] : {content : <History lang={lang}/>}
  }
  return (
    <Wrapper>
      <div className='slide-icon left'>
        <Icon token='arrow-left' width='24' height='24'/>
      </div>
      <Tab tabSet={tabSet} activeBgColor='#203B60'/>
      <div className='slide-icon right'>
        <Icon token='arrow-right' width='24' height='24'/>
      </div>
    </Wrapper>
  )
}