import styled from 'styled-components'
import { inject, observer } from 'mobx-react'
import './contractInfo.less'
import DeriNumberFormat from '../../../utils/DeriNumberFormat'
import Type from '../../../model/Type'
import UnderlineText from '../../../components/UnderlineText/UnderlineText'

const TokenWrapper = styled.span`
  margin-right : 4px;
  display: flex;
`
function ContractInfo({lang,trading}){
  const contract = trading.contract || {}
  const basetokenDisplay = (bTokenSymbol = []) => {
    return bTokenSymbol.length === 1 
          ? bTokenSymbol[0] 
          : bTokenSymbol.map((token,index) =>  {
            return (
             <TokenWrapper key={index}>{token}(<UnderlineText text={contract.bTokenMultiplier[index]} tip={lang['multiplier-tip']} width={375}/>)</TokenWrapper>
            )
          })
  }
  let rows = []
  if(Type.isFuture){
    rows = [
      {label : lang['base-token'],key : 'baseToken' , value : basetokenDisplay(contract.bTokenSymbol)},
      {label : lang['symbol'],key : 'symbol' ,value : contract['symbol'] },
      {label : lang['min-trade-volume'],key : 'multiplier', value :  (trading.contract['multiplier'] || '') + trading.symbolInfo.unit},
      {label : lang['funding-rate-coefficent'],key : 'fundingRateCoefficient' ,value : (contract['fundingRateCoefficient'] && (+contract['fundingRateCoefficient']).toExponential()),valueTip : trading.fundingCoefficientTip, tipWidth : 375},
      {label : lang['min-initial-margin-ratio'],key : 'minInitialMarginRatio',value : <DeriNumberFormat value={contract['initialMarginRatio'] * 100} decimalScale={0} suffix='%'/>},
      {label : lang['min-maintenance-margin-ratio'],key : 'minMaintenanceMarginRatio',value : <DeriNumberFormat value={contract['maintenanceMarginRatio'] * 100} decimalScale={0} suffix='%'/>},
      {label : lang['transaction-fee'],key : 'feeRatio',value : <DeriNumberFormat value={contract['feeRatio'] * 100} suffix='%'/>}
    ]
  } else {
    rows = [
      {label : lang['base-token'],key : 'baseToken' , value : basetokenDisplay(contract.bTokenSymbol)},
      {label : lang['symbol'],key : 'symbol' ,value : contract['symbol'] },
      {label : lang['underlier'],key : 'symbol' ,value : contract['underlier'] },
      {label : lang['strike'],key : 'symbol' ,value : contract['strike'] },
      {label : lang['option-type'],key : 'symbol' ,value : contract['optionType'] },
      {label : lang['min-trade-volume'],key : '', value :  (trading.contract['multiplier'] || '') + trading.symbolInfo.unit},
      {label : lang['min-initial-margin-ratio'],tip : trading.initialMarginRatioTip,tipWidth : '375',key : 'minInitialMarginRatio',
      value : <DeriNumberFormat value={contract['initialMarginRatio'] * 100} decimalScale={2} suffix='%'/>},
      {label : lang['min-maintenance-margin-ratio'],tip : trading.maintenanceMarginRatioTip,tipWidth : '375',key : 'minMaintenanceMarginRatio',
      value : <DeriNumberFormat value={contract['maintenanceMarginRatio'] * 100} decimalScale={2} suffix='%'/>},
      {label : lang['transaction-fee'],tip : trading.TransactionFeeTip,tipWidth : '320',key : 'feeRatio',value : trading.fee}
    ]
  }
  
  return (
    <div className='contract-info'>
      <div className='c-title'>{lang['contract-info']}</div>
      <div className='c-body'>
        {rows.map((row,index) => (
          <div className='c-row' key={index}>
            <div>{row.tip ? <UnderlineText text={row.label} multiline={true} tip={row.tip} width={row.tipWidth}/> : row.label} </div>
            <div className='c-value'>
              {row.valueTip ? <UnderlineText text={row.value} multiline tip={row.valueTip} width={row.tipWidth}/> : row.value}
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}
export default inject('trading')(observer(ContractInfo))