import React,{useState,useEffect} from "react";
import { Pie, PieChart, Cell,Label ,Tooltip} from "recharts";
import styled from "styled-components";
import UnderlineText from "../UnderlineText/UnderlineText";
import { bg } from "../../lib/web3js";
import DeriNumberFormat from "../../utils/DeriNumberFormat";

const Wrapper = styled.div`
  height: 100%;
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size : 24px;
  color : #E0ECFF;
  .unit {
    font-size: 12px;
    display: flex;
    margin-left: 2px;
    height: 24px;
    align-items: flex-end;
  }
  .text {
    display : flex;
  }
`

const CustomizedLabel = ({cx,cy,midAngle,innerRadius,outerRadius,value,index,...others}) => {
  const RADIAN = Math.PI / 180;
  const radius = 5+ innerRadius + (outerRadius - innerRadius);
  let x = cx + radius * Math.cos(-midAngle * RADIAN);
  let y = cy + radius * Math.sin(-midAngle * RADIAN);
  if(index === 0){
    x = 60;
    y = 135
  } else if( index ===1){
    x = 140
    y = 25
  } 
  else if (index === 2){
    x = 155;
    y = 135
  }
  return (
    <text x={x} y={y} fill="#93A1B8"
      textAnchor={x > cx ? "start" : "end"}
      dominantBaseline="central"
      fontSize='12px'
      width='100'
    >
      {perentage[index]}
    </text>
  );
}
const CustomTooltip = ({ value,tip }) => {
  return (
    <Wrapper>
      {value &&<UnderlineText tip={tip} multiline={true} width='375'>
        <DeriNumberFormat value={value} decaimalScale={0}/>
        <div className='unit'> %</div></UnderlineText>}
    </Wrapper>
  );
};
const perentage = ['0%','100%','200%']
export default function Gauge({accountInfo = {},lang}){
  const [data, setData] = useState([])
  const [coordinate, setCoordinate] = useState({})
  const [endColor, setEndColor] = useState('#FF8C39')
  const marginUsagePercent = isNaN(accountInfo.marginUsagePercent) ? '' : accountInfo.marginUsagePercent
  const tipContent = `${marginUsagePercent}% : ${lang['graph-current-margin-usage-tip']}`

  useEffect(() => {
    if(accountInfo && accountInfo.marginUsage && accountInfo.dynEffMargin){
      if(accountInfo.marginUsagePercent < 100 ){
        const marginUsage = bg(accountInfo.marginUsage).times(accountInfo.marginUsagePercent / 100)
        const data = (+accountInfo.dynEffMargin) === 0 
        ? 
          [
            {value : 1}
          ]
        :[
          { value: marginUsage.toNumber()},
          { value: bg(accountInfo.dynEffMargin).minus(marginUsage).toNumber()},
          { value: bg(accountInfo.dynEffMargin).div(2).toNumber()}
        ]
        setCoordinate({
          x1 : '0.5' ,
          y1 : '1',
          x2 : '1',
          y2 : "0.5"
        })
        setEndColor('#FF8C39');
        setData(data)
      } else {
        const diff = bg(accountInfo.marginUsagePercent).minus(100).toNumber()
        const overRange = bg(accountInfo.dynEffMargin).div(2)
        const data = [
          { value: bg(accountInfo.dynEffMargin).plus(overRange.times(diff).div(100)).toNumber()},
          { value : 0,},
          { value: overRange.times(100 - diff).div(100).toNumber()}
        ]
        setCoordinate({
          x1 : '0.5' ,
          y1 : '0.5',
          x2 : '1',
          y2 : "0.5"
        })
        setEndColor('#CE5E5E')
        setData(data)
      }
    }
  }, [accountInfo])
  return (
    <PieChart height={150} width={200}>
      <defs>
        <linearGradient
          id="usageMargin"
          {...coordinate}
        >
          <stop offset="0%" stopColor="#8EEC6D" />
          <stop offset="70%" stopColor={endColor} />
        </linearGradient>
        <linearGradient
          id="availMargin"
          gradientTransform="rotate(90deg)"
        >
          <stop offset="0.17%" stopColor="#43608D" />
          <stop offset="100.17%" stopColor="#3F4D8B" />
        </linearGradient>
      </defs>
      <Pie
        cy='55%'
        cx='55%'
        startAngle={225}
        endAngle={-45}
        innerRadius={'60%'}
        outerRadius={'80%'} 
        data={data}
        dataKey="value"
        labelLine={false}
        cornerRadius={5}
        stroke="none"
        isAnimationActive={false}
        label={<CustomizedLabel/>}
      >
        
        {accountInfo.dynEffMargin > 0 ? <>
          <Cell fill="url(#usageMargin)"  />
          <Cell fill="url(#availMargin)" fillOpacity='0.4' />
          <Cell fill="#CE5E5E" fillOpacity='0.15'/>
        </>
        :
          <Cell fill='rgb(89, 174, 153)' fillOpacity='0.1'/>
        }
      </Pie>
        <Tooltip cy='70%' content={<CustomTooltip tip={tipContent} value ={marginUsagePercent}/>} 
          wrapperStyle={{ visibility: "visible", pointerEvents: "auto",
          width : '100%',
          height : '100%',
          left: '5px',
          top: '0px' }}/>
        {/* <Cell component={<CustomTooltip value='0%'/>}/> */}
    </PieChart>
  )
};