import {useEffect,useState} from 'react'
import ReactDOM from 'react-dom'
import './symbolInfo.less'
import DeriNumberFormat from '../../../../utils/DeriNumberFormat'
import { firstLetterUppercase } from '../../../../utils/utils'
import Icon from '../../../../components/Icon/Icon'
import classNames from 'classnames'

export default function SymbolInfo({trading}){
  const [isShow, setIsShow] = useState(false)
  const rateClass = classNames('rate',{
    'up' : trading.symbolInfo.volumn24h > 0,
    'down' : trading.symbolInfo.volumn24h < 0
  })
  const showPanel = () => {
    setIsShow(!isShow)
  }
  const closeTooltip = () => setIsShow(false)
  useEffect(() => {
    closeTooltip()
    document.addEventListener('click',closeTooltip)
    return () => {
      document.removeEventListener('click',closeTooltip)
    }
  }, [trading.symbolInfo])
  return (
    <div className='symbol-info' >
      <div className='main' onClickCapture={showPanel} data-for='symbol-panel' data-event='click' data-tip='tooltip'>
        <div className='s-left'>
          <div>
            <div className='zone'>{firstLetterUppercase(trading.symbolInfo.zone)}</div>
            <div className='symbol-name'><Icon token={trading.symbolInfo.symbol}/>{trading.symbolInfo.symbol}</div>
          </div>
          <div className='select-icon'>
            <Icon token={isShow ? 'arrow-up' : 'arrow-down'} alt='select'/>
          </div>
          <div className='divider-line'></div>
        </div>
        <div className='index-area'>
          <div className='index-area-wraper'>
            <div className={rateClass}>
              {trading.symbolInfo.volumn24h && <Icon token={trading.symbolInfo.volumn24h > 0 ? 'rate-up' : 'rate-down'} alt='' />}
              <DeriNumberFormat value={trading.symbolInfo.volumn24h * 100} decimalScale={2} suffix='%'/></div>
            <div className='index-value'><DeriNumberFormat value={trading.symbolInfo.markPrice} decimalScale={2}/></div>
          </div>
        </div>
        <div className='kline-icon'>
          <Icon token='kline' width='24' height='24'/>
        </div>
      </div>
    </div>
  )
}