import React, { useEffect,useState } from 'react';
import './trade.less'
import TradeInfo from './TradeInfo/TradeInfo'
import Tradingview from './Tradingview/Tradingview'
import { inject, observer } from 'mobx-react';
import MyInfo from './MyInfo/MyInfo';
import ContractInfo from './ContractInfo/ContractInfo';
import Trades from './Trades/Trades';
import SymbolArea from './SideBar/SymbolArea/SymbolArea';
import SideBar from './SideBar/SideBar';

function Trade({trading,loading,wallet,version,type,lang}){
  const [loaded, setloaded] = useState(false)

  useEffect(() => {
    loading.loading()
    trading.init(wallet,() => {
      setloaded(true)
      // loading.loaded();
    })
    return () => { trading.clean()} ;
  }, [wallet.detail.account,version.current,type.current])

  return (
    <div className='trade-layout'>
      <div className='symbol-area'><SymbolArea lang={lang}/></div>
      <div className='sidebar'><SideBar lang={lang}/></div>
      <div className='trade-info'><TradeInfo lang={lang}/></div>
      <div className='kline'><Tradingview lang={lang} /></div>
      <div className='my-info'><MyInfo lang={lang}/></div>
      <div className='contract'><ContractInfo lang={lang}/></div>
      {/* <div className='trades'><Trades lang={lang}/></div> */}
    </div>
  )
}

export  default inject('wallet','type','version','loading','trading')(observer(Trade))
