import styled from "styled-components"
import Icon from "../../../components/Icon/Icon"
import classNames from "classnames"

const Wrapper = styled.div`
  &.trade-warnning {
    display: flex;
    visibility: hidden;
    height: 0;
    border : 1px dashed 
  }

  .content {
    font-size: 12px;
    margin-left: 12px;
    font-weight: 500;
  }
  
  &.show {
    visibility: visible;
    height: auto;
    opacity: 1; 
    animation: fadeIn 1s;
    padding: 16px 16px;
    border: 1px dashed #203B60;
  }
`
export default function TradeWarnning({content,className}){
  return (
      <Wrapper className={classNames('trade-warnning',className)}>
        <div className='icon'><Icon token='trade-warn'/></div>
        <div className='content' dangerouslySetInnerHTML={{__html : content}} ></div>
      </Wrapper>
    )
}